@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;700&display=swap");

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

main {
  background: linear-gradient(45deg, #000 0%, #111d38 100%);
  background-color: #000;
  padding: 18px;
}

.hero {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100vh;
  gap: 30px;
  @media (max-width: 400px) {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.fixed {
  position: fixed;
  top: 0;
  right: 17px;
  left: 0;
  bottom: 0;
}

.clicker {
  position: relative;
  background-color: yellowgreen;
  border: none;
  padding: 0.5rem 20px;
  margin: 1rem 0px;
  border-radius: 20px;
  box-shadow: 0px 4px rgb(57, 77, 18);
  min-width: 120px;
  outline: none;
  color: white;
  font-size: 1.2rem;
  font-weight: normal;
  font-family: "Oswald", "sans-serif";
}
.clicker:hover {
  cursor: pointer;
  background-color: rgb(125, 165, 45);
}
.clicker:active {
  transform: translateY(3px);
  box-shadow: 0px 2px rgb(57, 77, 18);
}

/* Default Styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Oswald", "sans-serif";
  color: whitesmoke;
  margin: 0;
}
p {
  font-family: "Arial";
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}
a {
  text-decoration: none;
}

@font-face {
  font-family: "A Goblin Appears";
  src: local("A Goblin Appears"), url("assets/fonts/A\ Goblin\ Appears.otf");
}
