@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --byte-grey: #929292;
  --byte-orange: #f4a41b;
  --byte-gold: #efc326;
  --shadow-green: rgb(32, 37, 32);
}

.download-line {
  width: 0%;
  height: 2px;
  transition: all 100ms ease;
}

.download:hover .download-line {
  width: 100%;
}

.social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 180px;
  margin-top: 10px;
}

.social-links a {
  color: whitesmoke;
  text-decoration: none;
}

.footer {
  height: max(10vh, 100px);
  color: var(--byte-grey);
  box-sizing: border-box;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

main {
  background: linear-gradient(45deg, #000 0%, #111d38 100%);
  background-color: #000;
  padding: 18px;
}

.hero {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100vh;
  grid-gap: 30px;
  gap: 30px;
  @media (max-width: 400px) {
    flex-direction: column-reverse;
    align-items: center;
  }
}

.fixed {
  position: fixed;
  top: 0;
  right: 17px;
  left: 0;
  bottom: 0;
}

.clicker {
  position: relative;
  background-color: yellowgreen;
  border: none;
  padding: 0.5rem 20px;
  margin: 1rem 0px;
  border-radius: 20px;
  box-shadow: 0px 4px rgb(57, 77, 18);
  min-width: 120px;
  outline: none;
  color: white;
  font-size: 1.2rem;
  font-weight: normal;
  font-family: "Oswald", "sans-serif";
}
.clicker:hover {
  cursor: pointer;
  background-color: rgb(125, 165, 45);
}
.clicker:active {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
  box-shadow: 0px 2px rgb(57, 77, 18);
}

/* Default Styles */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Oswald", "sans-serif";
  color: whitesmoke;
  margin: 0;
}
p {
  font-family: "Arial";
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}
a {
  text-decoration: none;
}

@font-face {
  font-family: "A Goblin Appears";
  src: local("A Goblin Appears"), url("/static/media/A Goblin Appears.4c9c1f1e.otf");
}

#chevron-down {
  position: relative;
  -webkit-animation-name: chevron-bounce;
          animation-name: chevron-bounce;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes chevron-bounce {
  0% {
    top: 0px;
  }
  50% {
    top: 10px;
  }
  100% {
    top: 0px;
  }
}

@keyframes chevron-bounce {
  0% {
    top: 0px;
  }
  50% {
    top: 10px;
  }
  100% {
    top: 0px;
  }
}

